<template>
  <div class="oc-container">
    <div class="oc-menu-main">
      <ul>
        <li
          v-for="(item,index) in menuList"
          :key="index"
          :class="{'active':index===menuMainActive}"
          @click="clickMenuMain(index)"
        >
          <el-tooltip effect="dark" :content="item.name" placement="right">
            <span>
              <i v-if="item.iconFont" :class="item.iconFont"></i>
              <i v-else-if="item.iconImg">
                <img :src="item.iconImg">
              </i>
              <i v-else class="el-icon-menu"></i>
            </span>
          </el-tooltip>
        </li>
      </ul>
    </div>
    <!-- main -->
    <div class="oc-menu-sub" v-if="hasSub()">
      <ul>
        <li
          v-for="(subItem,subIndex) in menuList[menuMainActive].sub"
          :key="subIndex"
          :class="{'active':subIndex===menuSubActive}"
        >
          <span @click="clickMenuSub(subIndex)">
            <i class="el-icon-arrow-down" v-if="hasSubChild(subIndex)"></i>
            {{subItem.name}}
          </span>
          <ul>
            <li></li>
          </ul>
        </li>
      </ul>
    </div>
    <!-- sub -->
  </div>
</template>

<script>
export default {
  name: 'OcMenu',
  data () {
    return {
      menuMainActive: 0,
      menuSubActive: 0,
      menuList: [{
        name: '首页',
        iconFont: 'el-icon-s-home',
        iconImg: '',
        url: '',
        sub: [{
          name: '测试测试测试测试测试测试测试测试测试测试测试测试测试测试测试测试',
          url: '',
          isOpenChild: false,
          child: [{
            name: '测试测试',
            url: ''
          }]
        },
        {
          name: '测试测试测试',
          url: '',
          isOpenChild: false,
          child: []
        },
        {
          name: '测试试测试',
          url: '',
          isOpenChild: false,
          child: []
        },
        {
          name: '测测试',
          url: '',
          isOpenChild: false,
          child: []
        },
        {
          name: '测试测试测试测试',
          url: '',
          isOpenChild: false,
          child: []
        },
        {
          name: '测试测测试',
          url: '',
          isOpenChild: false,
          child: []
        },
        {
          name: '测试测试',
          url: '',
          isOpenChild: false,
          child: []
        },
        {
          name: '测试测试',
          url: '',
          isOpenChild: false,
          child: []
        },
        {
          name: '测试测试',
          url: '',
          isOpenChild: false,
          child: []
        },
        {
          name: '测试测试',
          url: '',
          isOpenChild: false,
          child: []
        }]
      },
      {
        name: '默认图标',
        iconFont: '',
        iconImg: '',
        url: '',
        sub: []
      },
      {
        name: '图片图标都有优先图标',
        iconFont: 'el-icon-s-tools',
        iconImg: require('@/assets/images/logo.png'),
        url: '',
        sub: []
      },
      {
        name: '图片情况',
        iconFont: '',
        iconImg: require('@/assets/images/logo.png'),
        url: '',
        sub: []
      }]
    }
  },
  methods: {
    clickMenuMain (index) {
      this.menuMainActive = index
    },
    clickMenuSub (index) {
      this.menuSubActive = index
    },
    hasSub () {
      let isSub = this.menuList[this.menuMainActive].sub
      if (isSub && isSub.length > 0) {
        return true
      }
      return false
    },
    hasSubChild (index) {
      let isSubChild = this.menuList[this.menuMainActive].sub[index].child
      if (isSubChild && isSubChild.length > 0) {
        return true
      }
      return false
    }
  }
}
</script>

<style lang="scss" scoped>
.oc-container {
  position: relative;
  height: 100%;
  .oc-menu-main {
    position: relative;
    height: 100%;
    width: $--oc-menu-main-width;
    background: $--oc-color-theme;
    overflow: auto;
    z-index: 2;
    ul {
      li {
        width: 100%;
        height: 70px;
        transition: $--oc-all-transition;
        -moz-transition: $--oc-all-transition;
        -o-transition: $--oc-all-transition;
        -webkit-transition: $--oc-all-transition;
        cursor: pointer;
        &:hover {
          background: $--oc-color-theme-light-3;
        }
        &.active {
          background: $--oc-color-warning;
        }
        span {
          position: relative;
          width: 100%;
          height: 100%;
          display: block;
          i {
            position: absolute;
            top: 50%;
            left: 50%;
            display: block;
            width: 30px;
            height: 30px;
            margin-top: -15px;
            margin-left: -15px;
            text-align: center;
            line-height: 30px;
            font-size: 26px;
            color: #fff;
            img {
              display: block;
              width: 100%;
              height: 100%;
              pointer-events: none;
            }
          }
        }
      }
    }
  }
  .oc-menu-sub {
    position: absolute;
    top: 0;
    left: $--oc-menu-main-width;
    width: $--oc-menu-sub-width;
    height: 100%;
    padding: 14px 0;
    background: #fff;
    z-index: 1;
    overflow: auto;
    &>ul {
      &>li {
        &>span {
          position: relative;
          display: block;
          width: 100%;
          font-size: 14px;
          line-height: 20px;
          color: #666;
          padding: 10px 15px;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          word-break: break-all;
          font-weight: normal;
          transition: $--oc-all-transition;
          -moz-transition: $--oc-all-transition;
          -o-transition: $--oc-all-transition;
          -webkit-transition: $--oc-all-transition;
          cursor: pointer;
          i {
            display: block;
            float: right;
            line-height: 20px;
            padding: 10px 15px;
            margin: -10px -15px -10px auto;
            transition: $--oc-all-transition;
            -moz-transition: $--oc-all-transition;
            -o-transition: $--oc-all-transition;
            -webkit-transition: $--oc-all-transition;
            cursor: pointer;
          }
        }
        &:hover {
          &>span {
            color: $--oc-color-theme;
          }
        }
        &.active {
          &>span {
            color: $--oc-color-theme;
            background: $--oc-color-theme-light-8;
          }
        }
      }
    }
  }
}

</style>



