<template>
  <div class="layout">
    <div class="oc-header">
      <oc-header></oc-header>
    </div>
    <div class="oc-menu">
      <oc-menu></oc-menu>
    </div>
    <div class="oc-content">
      <router-view></router-view>
    </div>
    <div class="oc-footer">
      <oc-footer></oc-footer>
    </div>
  </div>
</template>

<script>
import OcHeader from '@/share/header'
import OcFooter from '@/share/footer'
import OcMenu from '@/share/menu'

export default {
  name: 'OcLayout',
  components: {
    OcHeader,
    OcFooter,
    OcMenu
  },
  data () {
    return {

    }
  }
}
</script>

<style lang="scss" scoped>
.oc-header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: $--oc-header-height;
  box-shadow: $--oc-box-shadow-light;
  -webkit-box-shadow: $--oc-box-shadow-light;
  z-index: 12;
}
.oc-footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  height: $--oc-footer-height;
  box-shadow: $--oc-box-shadow-base;
  -webkit-box-shadow: $--oc-box-shadow-base;
  z-index: 12;
}
.oc-menu {
  position: fixed;
  top: $--oc-header-height;
  left: 0;
  bottom: $--oc-footer-height;
  z-index: 11;
}
.oc-content {
  position: fixed;
  top: $--oc-header-height;
  right: 0;
  bottom: $--oc-footer-height;
  left: $--oc-menu-main-width;
  background: $--oc-background-color-base;
  overflow: auto;
  z-index: 10;
}
</style>



